.documentation{
  display: flex;
  justify-content: center;
}

.body_img{
  object-fit: cover;
  width: 10rem;
  height: 10rem;
  border-radius: .4rem;
}

.body_container{
  display: flex;
  flex-direction: row;
}

.body_content{
  width: 70%;
}

.body_spacer{
  width: 10%;
}

.body_img_container{
  width: 20%;
  display: flex;
  flex-direction: column;
}

.video-responsive {
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
  height: 0;
}

.video-responsive iframe {
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  position: absolute;
}

.original_model_container {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 0rem;
  grid-row-gap: 1rem;
}

.grid_img_cont_1{
  grid-area: 1 / 1 / 2 / 2;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.grid_img_cont_2{
  grid-area: 1 / 2 / 2 / 3;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.grid_img_cont_3{
  grid-area: 2 / 1 / 3 / 2;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.grid_img_cont_4{
  grid-area: 2 / 2 / 3 / 3;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.grid_img{
  object-fit: cover;
  width: 8rem;
  height: 8rem;
  border-radius: .4rem;
}

.designed_cont{
  display: flex;
}

.designed_text_area{
  width: 30%;
}

.designed_img_area{
  width: 70%;
}

.caption {
  color: #636363;
  font-weight: 400;
  text-decoration: none;
  vertical-align: baseline;
  font-size: 9pt;
  font-family: "Arial";
  font-style: italic;
  margin-top: .5em;
}

.home_cont {
  width: 85%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.page_space{
  display: flex;
  justify-content: center;
  height: 90vh;
}